<template>
    <div class="">
        <div class="flex_space bottom_border twoGrade_box">
            <span class="twoGrade_size blod" v-if="type==1">出款明细</span>
            <span class="twoGrade_size blod" v-if="type==2">回款明细</span>
            <div class="module_ckmx_picker module_ckmx_tab" :class="{'module_ckmx_picker_current':flag==3}">
                <span class="module_ckmx_tabitem" :class="{'current':flag==1}" @click="changeflag(1)">本季度</span>
                <span class="module_ckmx_tabitem" :class="{'current':flag==2}" @click="changeflag(2)">全年</span>
                <el-date-picker v-model="timerange" :editable='false' type="daterange" value-format="yyyy-MM-dd" range-separator="至" start-placeholder="选择日期" end-placeholder="选择日期" @change="changeflag(3)">
                </el-date-picker>
            </div>
        </div>
        <div class="table-infinite-box">
            <div class="table-infinite-top">
                <div class="table-normal-div">
                    <div>序号</div>
                    <div>投资基金</div>
                    <div>项目名称</div>
                    <div>投资项目轮次</div>
                    <div>投资额（万）</div>
                    <div>所占股比例</div>
                    <div>时间</div>
                    <div>操作</div>
                </div>
            </div>
            <div class="table-infinite-content">
                <!-- <div v-if="payoutlist.length==0" class="table-infinite-content-nodata">暂无数据</div> -->
                <div class="" v-for="(item,index) in payoutlist">
                    <div class="table-normal-div" v-if="!item.edit">
                        <div>{{item.seq}}</div>
                                               
                        <!-- 投资基金 -->
                        <div>{{item.fundName}}</div>   
                        <!-- 项目名称 -->
                        <div>{{item.name}}</div>                     
                        <!-- 投资项目轮次 -->
                        <div>{{item.turn}}</div>                        
                        <!-- 投资额 -->
                        <div>{{item.figure}}</div>                        
                        <!-- 占股比例 -->
                        <div>{{item.stock_scale}}</div>                       
                        <!-- 时间 -->
                        <div>{{item.date}}</div>
                        <!-- 操作 -->
                        <div>
                            <a class="table-link" @click="editPayout(index)">编辑</a>
                            <a class="table-link" @click="delPayout(item.id)">删除</a>
                        </div>   
                    </div>
                    <div class="table-normal-div" v-else>
                        <div>{{item.seq}}</div>
                        <!-- 投资基金 -->
                        <div>{{item.fundName}}</div>   
                        <!-- 项目名称 -->
                        <div>{{item.name}}</div>                     
                        <!-- <div class="module_ckmx_td_input">
                            <el-select v-model="item.fundName" placeholder="请选择" @change="changeFundOrPro($event,index,'fundId')">
                                <el-option
                                  v-for="fund in fundlist"
                                  :key="fund.id"
                                  :label="fund.name"
                                  :value="fund.id">
                                </el-option>
                            </el-select>
                        </div>
                        <div class="module_ckmx_td_input">
                            <el-select v-model="item.name" placeholder="请选择" 
                            @change="changeFundOrPro($event,index,'projId')">
                                <el-option
                                  v-for="pro in projectlist"
                                  :key="pro.id"
                                  :label="pro.name"
                                  :value="pro.id">
                                </el-option>
                            </el-select>
                        </div> -->
                        <div class="module_ckmx_td_input">
                            <el-select v-model="item.turn" placeholder="请选择">
                                <el-option
                                  v-for="turn in turnlist"
                                  :key="turn.value"
                                  :label="turn.label"
                                  :value="turn.value">
                                </el-option>
                            </el-select>
                        </div>
                        <div class="module_ckmx_td_input">
                            <el-input v-model="item.figure" placeholder="请输入内容"></el-input>
                        </div>
                        <div class="module_ckmx_td_input">
                            <el-input v-model="item.stock_scale" placeholder="请输入内容"></el-input>
                        </div>
                        <div class="module_ckmx_td_input">
                            <el-date-picker :editable='false' v-model="item.date" value-format="yyyy-MM-dd" type="date" placeholder="选择日期">
                            </el-date-picker>
                        </div>
                        <div>
                            <a class="table-link" @click="saveeditPayout(index)">保存</a>
                            <a class="table-link" @click="cancleeditPayout(index)">取消</a>
                        </div>
                    </div>
                    <!-- 无限下拉加载 -->
                </div>
                <div v-infinite-scroll="getListData" infinite-scroll-distance="10">
                </div>  
            </div>
            <div class="table-infinite-footer">
                <div class="table-normal-div">
                    <div>新增</div>
                    
                    <div class="module_ckmx_td_input">
                        <el-select v-model="newPayout.fundId" placeholder="请选择基金">
                            <el-option
                              v-for="fund in fundlist"
                              :key="fund.id"
                              :label="fund.name"
                              :value="fund.id">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="module_ckmx_td_input">
                        <el-select v-model="newPayout.projId" placeholder="请选择项目">
                            <el-option
                              v-for="pro in projectlist"
                              :key="pro.id"
                              :label="pro.name"
                              :value="pro.id">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="module_ckmx_td_input">
                        <el-select v-model="newPayout.turn" placeholder="请选择轮次">
                            <el-option
                              v-for="turn in turnlist"
                              :key="turn.value"
                              :label="turn.label"
                              :value="turn.value">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="module_ckmx_td_input">
                        <el-input v-model="newPayout.figure" placeholder="请输入投资额"></el-input>
                    </div>
                    <div class="module_ckmx_td_input">
                        <el-input v-model="newPayout.stock_scale" placeholder="请直接输数字，不加’%‘，例：1"></el-input>
                    </div>
                    <div class="module_ckmx_td_input">
                        <el-date-picker  :editable='false' v-model="newPayout.date" type="date"  value-format="yyyy-MM-dd" placeholder="选择日期">
                        </el-date-picker>
                    </div>
                    <div>
                        <a class="table-link" @click="addPayout">添加</a>
                        <a class="table-link" @click="clearPayout">清除</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {eachFundRunDetail,allProjectOrFund,operateFundRunDetail} from '../../config/api'
    import {
        dialogConfirm,
        dialogConfirmHasbtn,
        compareJson
    } from '../../config/mUtils'
    export default {
        inject:['reload'],
        data() {
            return {
                //type:1,//1.出款，2.回款
                flag:1,//1.本季度，2.全年，3.指定时间段
                timerange:'',
                startTime:'',
                endTime:'',
                //项目列表
                projectlist:[],
                //基金列表
                fundlist:[],
                //轮次选择列表
                turnlist: [],
                newPayout:{
                    fundId:'',
                    projId:'',
                    turn:'',
                    figure:'',
                    stock_scale:'',
                    date:''
                },
                oldPayout:{

                },
                //分页数据
                pageSize:10,
                currentPage:1,
                hasMoredata:true,
                
                value: '',
                value1:'',
                value6:'',
                input:'',
                payoutlist:[]
            }
        },
        props: ['type'],//type=>1出款 2回款
        components: {
            
        },
        created() {
            this.setElement();
            this.geteachFundRunDetail(true);
            this.getProOrFundlist();
            this.turnlist=[{
              value: '种子轮',
              label: '种子轮'
            }, {
              value: '天使轮',
              label: '天使轮'
            }, {
              value: 'Pre-A轮',
              label: 'Pre-A轮'
            }, {
              value: 'A轮',
              label: 'A轮'
            }, {
              value: 'A+轮',
              label: 'A+轮'
            }, {
              value: 'Pre-B轮',
              label: 'Pre-B轮'
            }, {
              value: 'B轮',
              label: 'B轮'
            }, {
              value: 'B+轮',
              label: 'B+轮'
            }, {
              value: 'C轮',
              label: 'C轮'
            }, {
              value: 'C+轮',
              label: 'C+轮'
            }, {
              value: 'D轮',
              label: 'D轮'
            }, {
              value: 'E轮及以后',
              label: 'E轮及以后'
            }]
        },
        computed: {},
        mounted() {

        },
        methods: {
            //获取出款或回款明细
            geteachFundRunDetail(fresh){
                if(fresh){
                    this.payoutlist=[];
                    this.hasMoredata=true;
                    this.currentPage=1;
                }
                if(this.hasMoredata){
                    eachFundRunDetail({
                        flag:this.flag,
                        startTime:this.startTime,
                        endTime:this.endTime,
                        type:this.type,
                        number:this.pageSize,
                        page:this.currentPage
                    }).then(res=>{
                        if(res.data.msgNo==1){
                            let pl=res.data.payload;
                            if(pl.length<this.pageSize){
                                this.hasMoredata=false;
                            }else{
                                this.hasMoredata=true;
                            }
                            pl.forEach(item=>{
                                item.edit=false;
                            })
                            this.payoutlist=this.payoutlist.concat(pl);
                        }
                    })
                }else{

                }
                
            },
            //获取项目/基金列表
            getProOrFundlist(){
                allProjectOrFund({
                    type:1,
                    flag:0
                }).then(res=>{
                    if(res.data.msgNo==1){
                        this.projectlist=res.data.payload.data;
                    }
                });
                allProjectOrFund({
                    type:2
                }).then(res=>{
                    if(res.data.msgNo==1){
                        this.fundlist=res.data.payload.data;
                    }
                });

            },
            //下拉加载
            getListData(){
                if(this.hasMoredata){
                    this.currentPage++;
                    this.geteachFundRunDetail(false);
                }
            },
            //编辑
            editPayout(index){
                this.payoutlist[index].edit=true;
                let num=this.payoutlist[index].stock_scale.toString().split('%')
                this.payoutlist[index].stock_scale=parseFloat(num[0]);
                this.oldPayout=JSON.parse(JSON.stringify(this.payoutlist[index]));
            },
            changeFundOrPro(e,index,name){
                this.payoutlist[index][name]=e
            },
            //保存修改
            saveeditPayout(index){
                let flag=compareJson(this.payoutlist[index],this.oldPayout);
                if(flag==false){
                    this.editPayoutfun(2,this.payoutlist[index].id,this.payoutlist[index]);
                }else{
                    this.oldPayout={};
                    this.payoutlist[index].edit=false;
                    this.payoutlist[index].stock_scale=this.payoutlist[index].stock_scale+'%'
                }
            },
            cancleeditPayout(index){
                let flag=compareJson(this.payoutlist[index],this.oldPayout);
                if(flag==false){
                    dialogConfirmHasbtn('确认放弃修改吗？').then(res=>{
                        for(let item in this.payoutlist[index]){
                            this.payoutlist[index][item]=this.oldPayout[item];
                        }
                        this.oldPayout={};
                        this.payoutlist[index].edit=false;
                        this.payoutlist[index].stock_scale=this.payoutlist[index].stock_scale+'%'
                    }).catch(()=>{})
                }else{
                    this.oldPayout={};
                    this.payoutlist[index].edit=false;
                    this.payoutlist[index].stock_scale=this.payoutlist[index].stock_scale+'%'
                }
            },
            delPayout(id){
                this.editPayoutfun(3,id,this.newPayout);
            },
            addPayout(){
                this.editPayoutfun(1,'',this.newPayout);
            },
            clearPayout(){
                this.newPayout={
                    fundId:'',
                    projId:'',
                    turn:'',
                    figure:'',
                    stock_scale:'',
                    date:''
                };
            },
            editPayoutfun(flag,id,newPayout){
                let np=JSON.parse(JSON.stringify(newPayout));
                let param={};
                if(flag!=3){
                    if(np.projId==''||np.fundId==''||np.turn==''||np.stock_scale==''||np.figure==''||np.date==''){
                        dialogConfirm('info','您还有必填项没有填～');
                        return;
                    }else{
                        if(isNaN(parseFloat(np.figure))||isNaN(parseFloat(np.stock_scale))){
                            dialogConfirm('info','投资额或占股比例只能输入数字～');
                            return;
                        }else if(np.stock_scale>100){
                            dialogConfirm('info','占股比例不能大于100');
                            return;
                        }
                    }
                    param=np;
                }else{
                }
                param.id=id;
                param.type=this.type;
                param.flag=flag;
                param.figure=parseFloat(param.figure);
                param.stock_scale=parseFloat(param.stock_scale)+'%';
                operateFundRunDetail(param).then(res=>{
                    if(res.data.msgNo==1){
                        if(flag==3){
                            dialogConfirm('success','删除成功');
                        }else if(flag==1){
                            dialogConfirm('success','添加成功');
                        }else if(flag==2){
                            dialogConfirm('success','修改成功');
                        }
                        this.clearPayout();
                        this.reload();
                        this.geteachFundRunDetail(true);
                    }else{
                        dialogConfirm('fail',res.data.message);
                    }
                })
            },
            
            //修改flag
            changeflag(flag){
                this.flag=flag;
                if(flag==3){
                    if(this.timerange==''||this.timerange==null||this.timerange==undefined){
                        this.flag=1;
                        this.startTime='';
                        this.endTime='';
                    }else{
                        this.startTime=this.timerange[0];
                        this.endTime=this.timerange[1];
                    }
                    
                }else{
                    this.timerange='';
                    this.startTime='';
                    this.endTime='';
                }
                this.geteachFundRunDetail(true);
            },
            setElement(){
                let dom1 = document.getElementsByClassName('el-progress-circle__track');
                let dom2 = document.getElementsByClassName('el-progress-circle__path');
                for(let i =0;i<dom1.length;i++){
                    dom1[i].setAttribute('stroke','#A1D3FF');
                    dom2[i].setAttribute('stroke-linecap','');
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../style/folder.scss';
    @import '../../style/fund.scss';
    .table-normal-div>div:nth-child(1){
    		width: 10%;
    }
    .table-normal-div>div:nth-child(2){
    		width: 20%;
    }
    .table-normal-div>div:nth-child(3){
    		width: 20%;
    }
    .table-normal-div>div:nth-child(4){
    		width: 10%;
    }
    .table-normal-div>div:nth-child(5){
    		width: 10%;
    }
    .table-normal-div>div:nth-child(6){
    		width: 10%;
    }
    .table-normal-div>div:nth-child(7){
    		width: 10%;
    }
    .table-normal-div>div:nth-child(8){
    		width: 10%;
    }
</style>



