	<div class="">
        <div class="fund-linebar marginb_10">
            <div class="fund-linebar-item"  :style="{background:colorlist[index],width:item.voteRatio}" v-for="(item,index) in data">{{item.voteRatio}}</div>
        </div>
        <div class="fund-linespan-top">
            <div class="fund-linespan-top-item" v-for="(item,index) in data">
                <span class="span-circle" :style="{background:colorlist[index]}"></span>
                <em>{{item.name}}</em>
            </div>
        </div>
    </div>
</template>
<script>
    import {
        dialogConfirm,
        dialogConfirmHasbtn
    } from '../../config/mUtils'
    export default {
        data() {
            return {
                colorlist:['#85A5D9','#8DCCF7','#E8737B','#77C8D2','#F9A791','#FFAD63','#F9C495','#F1DAA1','#FFE9B3','#EBF4B7','#B6E8AF','#79C7AE','#87DBC0']
            }
        },
        components: {},
        props: ['data'],//type=>1出款 2回款
        created() {
        },
        computed: {},
        mounted() {
           
        },
        methods: {
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../style/fund.scss';
    .fund-items{
        justify-content:space-between;
    }
</style>


