<template>
    <div class="">
        <div class="iconAddInfo fund-item-scale left"><span></span>募集规模(元)：5000,000</div>
        <div class="clear"></div>
        <div class="fund-linebar">
            <div class="fund-linebar-item">15%</div>
            <div class="fund-linebar-item">15%</div>
