        </div>
        <div class="fund-linespan-top">
            <div class="fund-linespan-top-item">
                <span class="span-circle"></span>
                <em>xxx公司</em>
            </div>
            <div class="fund-linespan-top-item">
                <span class="span-circle bg_red"></span>
                <em>xxx公司</em>
            </div>
        </div>
    </div>
</template>

<script>
    import {} from '../../config/api'
    import {
        dialogConfirm,
        dialogConfirmHasbtn
    } from '../../config/mUtils'
    export default {
        data() {
            return {
                value6:'',
            }
        },
        components: {},
        created() {
            
        },
        computed: {},
        mounted() {
            this.setElement();
        },
        methods: {
            setElement(){
                let dom1 = document.getElementsByClassName('el-progress-circle__track');
                let dom2 = document.getElementsByClassName('el-progress-circle__path');
                for(let i =0;i<dom1.length;i++){
                    dom1[i].setAttribute('stroke','#A1D3FF');
                    dom2[i].setAttribute('stroke-linecap','');
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../style/fund.scss';
</style>



