<template>
  <div class="">
    <div class="fund_items_column flex flex_wrap">
      <div class="fund_item_column flex flex_column marginb_20" v-for="item in fundlist">
        <div class="right_top_span" v-if="item.status==1">在售</div>
        <div class="right_top_span right_top_span_gray" v-else>售罄</div>
        <div class="fund_item_row flex_column fund_item_row1">
          <div class="fsize_20 f_bold marginb_10">{{item.fundName}}</div>
          <div class="fsize_14 fcolor_gray marginb_10">{{item.regTime}}</div>
        </div>
        <div class="fund_item_row flex flex_wrap fund_item_row3">
          <div class="border_span border_span_yellow" v-if="item.type==1">储备</div>
          <div class="border_span border_span_red" v-if="item.type==2">存续</div>
          <div class="border_span border_span_purple" v-if="item.type==3">退出</div>
          <div class="border_span border_span_gray">存续期限(年)<span>{{item.duration}}</span></div>
          <div class="border_span border_span_gray">{{item.direction}}</div>
        </div>
        <div class="fund_item_row flex fund_item_row2">
          <div class="fund_item_row flex_column flex_grow fund_width">
            <div class="fcolor_black fsize_34 fund_text_no_wrap">{{item.contributeSize | NumFormat}}</div>
            <div class="fcolor_gray fsize_14">认缴规模（元）</div>
          </div>
          <div class="fund_item_row flex_column flex_grow fund_width">
            <div class="fcolor_orange fsize_34 fund_text_no_wrap">{{item.actualSize | NumFormat}}</div>
            <div class="fcolor_gray fsize_14">实缴规模（元）</div>
          </div>
        </div>
        <!-- <div class="fund_item_row flex_center flex_justifycenter fund_item_row4">
          <div class="flex fund_item_btnbox">
            <div class="fund_item_btn fund_item_btn_red" @click="downloadFundFile">下载模版</div>
            <div class="fund_item_btn">
              <input type="file" ref="inputer" class="fund_item_btn_inputfile" v-on:change="uploadFundExcel($event,item.id)" />导入基金数据</div>
          </div>
        </div> -->
      </div>
    </div>
  </div>
</template>
<script>
import { fundSurvey, downloadFundAllExcel, fundAllExcel } from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      fundlist: []
    }
  },
  components: {},
  created() {
    fundSurvey({

    }).then(res => {
      if (res.data.msgNo == 1) {
        let fl = res.data.payload;
        fl.forEach(item=>{
          if(item.regTime==''||item.regTime==null){
            item.regTime='------'
          }
        })
        this.fundlist = fl;
      }
    })
  },
  computed: {},
  mounted() {
    this.setElement();
  },
  methods: {
    //导入Excel文件
    uploadFundExcel(e, id) {
      // 通过DOM取文件数据
      let file = e.target.files[0];
      if (file == undefined) {
        return false;
      }
      // console.log(file);
      if (!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
        dialogConfirm('info', '请选择Excel格式文件')
        inputDOM.value = '';
        return false
      }
      fundAllExcel({
        fund_id: id
      }, file).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '导入成功');
        } else {
          dialogConfirm('info', res.data.message);
        }
        e.target.value = ''
      })
    },
    //下载模版
    downloadFundFile() {
      downloadFundAllExcel().then(res => {
        if (res.data.msgNo == 1) {
          window.location.href = res.data.payload;
        } else {
          dialogConfirm('info', res.data.message)
        }
      });
    },
    setElement() {
      let dom = document.getElementsByClassName('el_progress_blue');
      for (let j = 0; j < dom.length; j++) {
        let dom1 = dom[j].getElementsByClassName('el-progress-circle__track');
        let dom2 = dom[j].getElementsByClassName('el-progress-circle__path');
        for (let i = 0; i < dom1.length; i++) {
          dom1[i].setAttribute('stroke', '#A1D3FF');
          dom2[i].setAttribute('stroke-linecap', '');
        }
      }
      let dom3 = document.getElementsByClassName('el_progress_red');
      for (let j = 0; j < dom3.length; j++) {
        let dom4 = dom3[j].getElementsByClassName('el-progress-circle__track');
        let dom5 = dom3[j].getElementsByClassName('el-progress-circle__path');
        for (let i = 0; i < dom4.length; i++) {
          dom4[i].setAttribute('stroke', '#FFE6E6');
          dom5[i].setAttribute('stroke-linecap', '');
        }
      }
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/fund.scss';
.fund_width{
	width: 50%;
}
.fund_text_no_wrap{
	width: 100%;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}
.fund_item_row1 {
  position: relative;
}

.border_span {
  border: 1px solid #FF7979;
  background: rgba(255,121,121,0.1);
  color: #FF7979;
  font-size: 14px;
  height: 20px;
  line-height: 18px;
  padding: 0px 5px;
  margin-right: 20px;
}
.border_span_yellow{
  background: rgba(245,196,35,0.1);
  border-color: #F5C423;
  color: #F5C423;
}
.border_span_red{
  background: rgba(255,121,121,0.1);
  border-color: #FF7979;
  color: #FF7979;
}
.border_span_purple{
  background: rgba(127,79,156,0.1);
  border-color: #7F4F9C;
  color: #7F4F9C;
}
.border_span_gray{
  background: #fff;
  border-color: #7C90A1;
  color: #7C90A1;
  span{
    color: #333;
  }
}
.right_top_span {
  font-size: 20px;
  color: #fff;
  height: 90px;
  line-height: 70px;
  width: 100px;
  text-align: center;
  border-radius: 1px;
  background: url('../../images/showAllFundInfo_itemspan.png');
  background-size: cover;
  position: absolute;
  left: -40px;
  top: 30px;
}

.right_top_span_gray {
  background: url('../../images/showAllFundInfo_itemspan1.png');
  background-size: cover;
}

.fund_item_btnbox {
  height: 30px;
  .fund_item_btn {
    font-size: 14px;
    color: #FE5229;
    padding: 0px 8px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #FE4A2C;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    .fund_item_btn_inputfile {
      position: absolute;
      left: 0px;
      right: 0px;
      top: 0px;
      bottom: 0px;
      opacity: 0;
      cursor: pointer;
    }
  }
  .fund_item_btn_red {
    color: #fff;
    background: url('../../images/showAllFundInfo_itembg.png') no-repeat;
    background-size: cover;
  }
}

</style>



