                <div class="fund-linespan-dashline"></div>
            </div>
            <div class="fund-spanbox fund-spanbox-center">
                <div class="fund-spanbox-line" v-for="item in data"></div>
            </div>
            <div class="fund-spanbox">
                <div class="" :class="{'fund-spanitem-red':item.type==1,'fund-spanitem':item.type==2}" 
                :style="{height:item.height}" v-for="item in data">
                    <span class="count">{{item.figure | NumFormat}}</span>
                    <span class="time">{{item.date}}</span>
                    <span class="line"></span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {
        dialogConfirm,
        dialogConfirmHasbtn
    } from '../../config/mUtils'
    export default {
        data() {
            return {

            }
        },
        components: {},
        props: ['data'],//type=>1出款 2回款
        created() {
        },
        computed: {},
        mounted() {
           
        },
        methods: {
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../style/fund.scss';
    .fund-items{
        justify-content:space-between;
    }
</style>


