<template>
  <div class="personal-content-body">
    <div class="content-box">
      <div class="module-box module-zl">
        <div class="flex_space module-zl-title">
          <span class="oneGrade_size blod">基金进出款</span>
        </div>
        <div class="flex_space bottom_border twoGrade_box marginb_20">
          <span class="twoGrade_size blod">基金概览</span>
          <div class="iconAddInfo twoGrade-btn" @click="goCreatnew"><span></span>添加新基金</div>
        </div>
        <fund-view></fund-view>
      </div>
      <div class="module-box module-ckmx">
        <payout-detail class="module-ckmx-box" type="1"></payout-detail>
        <payout-detail class="module-ckmx-box" type="2"></payout-detail>
      </div>
      <payout-count></payout-count>
    </div>
  </div>
</template>
<script>
import {} from '../../config/api'
import fundView from '../../pages/fundrunComponent/fundView1.vue'
import payoutDetail from '../../pages/fundrunComponent/payoutDetail.vue'
import payoutCount from '../../pages/fundrunComponent/payoutCount.vue'
import votedProjectdetail from '../../pages/fundrunComponent/votedProjectdetail.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      value6: '',
    }
  },
  components: {
    fundView,
    payoutCount,
    payoutDetail,
    votedProjectdetail,
  },
  created() {

  },
  computed: {},
  mounted() {
    this.setElement();
  },
  methods: {
    //去新建一行
    goCreatnew() {
      this.$router.push({
        path: '/home/fundManageDetail',
        query: {
          path: {
            from: '/home/showAllFundInfo',
            name: '基金运行总览'
          }
        }
      })
    },
    setElement() {
      let dom = document.getElementsByClassName('el_progress_blue');
      for (let j = 0; j < dom.length; j++) {
        let dom1 = dom[j].getElementsByClassName('el-progress-circle__track');
        let dom2 = dom[j].getElementsByClassName('el-progress-circle__path');
        for (let i = 0; i < dom1.length; i++) {
          dom1[i].setAttribute('stroke', '#A1D3FF');
          dom2[i].setAttribute('stroke-linecap', '');
        }
      }
      let dom3 = document.getElementsByClassName('el_progress_red');
      for (let j = 0; j < dom3.length; j++) {
        let dom4 = dom3[j].getElementsByClassName('el-progress-circle__track');
        let dom5 = dom3[j].getElementsByClassName('el-progress-circle__path');
        for (let i = 0; i < dom4.length; i++) {
          dom4[i].setAttribute('stroke', '#FFE6E6');
          dom5[i].setAttribute('stroke-linecap', '');
        }
      }
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/folder.scss';
@import '../../style/fund.scss';
.content-box {
  //min-width: 1300px!important;
}

.module-ckmx-box {
  margin-bottom: 20px;
}

</style>



