<template>
    <div class="module-box module-ckmx">
        <div class="flex_space_normal module-zl-title module_ckmx_picker module_ckmx_picker_big">
            <span class="oneGrade_size blod marginr_10">选择基金</span>
            <el-select v-model="fundId" placeholder="请选择" 
            @change="changeFundOrPro">
                <el-option
                  v-for="pro in fundList"
                  :key="pro.id"
                  :label="pro.name"
                  :value="pro.id">
                </el-option>
            </el-select>
        </div>
        <div class="flex_space bottom_border twoGrade_box">
            <span class="twoGrade_size blod">出款统计</span>
        </div>
        <div class="fund-items marginb_20">
            <div class="fund-item">
                <div class="iconAddInfo fund-item-scale"><span></span>总出款：{{totalGoOut.totalGoOut | NumFormat}}</div>
                <div class="iconAddInfo flex_space el_progress_blue">
                    <el-progress type="circle" :percentage="totalGoOut.totalRate" color="#3C98E8" :width='100' :stroke-width='10' ref='progress'></el-progress>
                    <div class="progress-right-box">
                        <div class="iconAddInfo progress-right progress-right-zs "><span></span>基金总数</div>
                        <div class="iconAddInfo progress-right progress-right-ck" ><span></span>总出款</div>
                    </div>
                </div>
            </div>
            <div class="fund-item">
                <div class="iconAddInfo fund-item-scale  fund-item-scale-red"><span></span>本年内出款：{{thisYearGoOut.thisYearGoOut | NumFormat}}</div>
                <div class="iconAddInfo flex_space el_progress_red">
                    <el-progress type="circle" :percentage="thisYearGoOut.goOutRate" color="#FF7676" :width='100' :stroke-width='10' ref='progress'></el-progress>
                    <div class="progress-right-box">
                        <div class="iconAddInfo progress-right progress-right-zs "><span></span>本年内总出款</div>
                        <div class="iconAddInfo progress-right progress-right-ck" ><span></span>本年内出款数</div>
                    </div>
                </div>
            </div>
            <div class="fund-item">
                <div class="iconAddInfo fund-item-scale fund-item-scale-red"><span></span>本年内回款：{{thisYearGoIn.thisYearGoIn | NumFormat}}</div>
                <div class="iconAddInfo flex_space el_progress_red">
                    <el-progress type="circle" :percentage="thisYearGoIn.goInRate" color="#FF7676" :width='100' :stroke-width='10' ref='progress'></el-progress>
                    <div class="progress-right-box">
                        <div class="iconAddInfo progress-right progress-right-zs "><span></span>本年回款总数</div>
                        <div class="iconAddInfo progress-right progress-right-ck" ><span></span>本年内回款数</div>
                    </div>
                </div>
            </div>
        </div>
        <fund-line-span :data="detail" v-if="detail.length!=0"></fund-line-span>
        <div class="flex_space bottom_border twoGrade_box">
            <span class="twoGrade_size blod">基金已投项目详情</span>
        </div>
        <div v-if="!projType_nodata">
            <div class="voted-project-box">
                <div>
                    <div class="iconAddInfo fund-item-scale left"><span></span>已投出总金额：{{fundVoteProj.fundVoteTotal | NumFormat}}</div>
                    <div class="clear"></div>  
                </div>
                <div class="fsize_16 marginb_10">
                    各项目出资占比
                </div>
                <fund-line-bar :data="fundVoteProj.rectangle"></fund-line-bar>
            </div>
            
            <div class="flex_space bottom_border twoGrade_box">
                <span class="twoGrade_size blod">在投项目</span>
            </div>
            <div v-for="item in castProj" class="voted-project-box" v-if="castProj.length!=0">
                <div class="twoGrade_size">{{item.name}}</div>
                <div>
                    <div class="iconAddInfo fund-item-scale left"><span></span>已投出金额：{{item.voteMoney | NumFormat}}</div>
                    <div class="clear"></div>
                </div>
                <fund-line-span :data="item.timeAxis"></fund-line-span>
            </div>
            <div class="table-nodata" v-if="castProj.length==0">
                <img src="../../images/table_nodata.png"> 没有对应数据
            </div>
            <div class="flex_space bottom_border twoGrade_box">
                <span class="twoGrade_size blod">退出项目</span>
            </div>
            <div v-for="item in exitProj" class="voted-project-box" v-if="exitProj.length!=0">
                <div class="twoGrade_size">{{item.name}}</div>
                <div>
                    <div class="iconAddInfo fund-item-scale left"><span></span>已投出金额：{{item.voteMoney | NumFormat}}</div>
                    <div class="clear"></div>
                </div>
                <fund-line-span :data="item.timeAxis"></fund-line-span>
                <div class="fund_yieldrate">
                    <div class="fund_yieldrate_item" v-for="item1 in item.rateDatalist">
                        <div class="title">{{item1.name}}<span class=""></span></div>
                        <div class="number" :class="{'number_green':item1.type==-1}">{{item1.num}}</div>
                        <div class="yieldrate_box">
                            <div class="yieldrate_over yieldrate_over_orange" :style="{width:item1.yieldrate_over_orange}"></div>
                            <div class="yieldrate_over yieldrate_over_green" :style="{width:item1.yieldrate_over_green}"></div>
                            <div class="yieldrate_normal yieldrate_normal_red" :style="{width:item1.yieldrate_normal_red}"></div>
                            <div class="yieldrate_normal yieldrate_normal_green" :style="{width:item1.yieldrate_normal_green}"></div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="table-nodata" v-if="exitProj.length==0">
                <img src="../../images/table_nodata.png"> 没有对应数据
            </div>
        </div>
        <div class="table-nodata" v-if="projType_nodata">
            <img src="../../images/table_nodata.png"> 没有对应数据
        </div>
    </div>
</template>

<script>
    import {fundIndicator,allProjectOrFund} from '../../config/api'
    import votedProjectdetail from '../../pages/fundrunComponent/votedProjectdetail.vue'
    import fundLineSpan from '../../pages/fundrunComponent/fundLineSpan.vue'
    import fundLineBar from '../../pages/fundrunComponent/fundLineBar.vue'
    import {
        dialogConfirm,
        dialogConfirmHasbtn
    } from '../../config/mUtils'
    export default {
        data() {
            return {
                fundId:'',//已选基金Id
                fundName:'',//已选基金Id
                hasnodata:true,
                fundList:[],//基金列表
                //出款统计
                totalGoOut:'',//总出款
                thisYearGoOut:'',//本年内出款
                thisYearGoIn:'',//本年内回款
                detail:'',//基⾦出款回款记录明细
                fundVoteProj:'',//基金已投项目详情
                projType_nodata:false,
                castProj:'',//在投项目
                exitProj:'',//退出项目
            }
        },
        components: {votedProjectdetail,fundLineSpan,fundLineBar},
        created() {
            this.getProOrFundlist();
        },
        computed: {},
        mounted() {
            this.setElement();
        },
        methods: {
            getData(){
                fundIndicator({
                    fundId:this.fundId
                }).then(res=>{
                    if(res.data.msgNo==1){
                        if(res.data.payload==null||res.data.payload==''||res.data.payload==undefined){
                            this.hasnodata=true;
                        }else{
                            this.hasnodata=false;
                            //出款统计
                            this.totalGoOut=res.data.payload.goOutStatistical.total;
                            this.totalGoOut.totalRate=this.clearPercent(this.totalGoOut.totalRate)
                            this.thisYearGoOut=res.data.payload.goOutStatistical.thisYearGoOut;
                            this.thisYearGoOut.goOutRate=this.clearPercent(this.thisYearGoOut.goOutRate)
                            this.thisYearGoIn=res.data.payload.goOutStatistical.thisYearGoIn;
                            this.thisYearGoIn.goInRate=this.clearPercent(this.thisYearGoIn.goInRate)

                            this.fundVoteProj=res.data.payload.fundVoteProj;
                            if(res.data.payload.projType!=null){
                                this.projType_nodata=false;
                                let cp=res.data.payload.projType.castProj;
                                if(cp==undefined||cp==null||cp==''){
                                    cp=[];
                                }
                                cp.forEach(item=>{
                                    item.timeAxis=this.changelinespandata(item.timeAxis);
                                })
                                this.castProj=cp;
                                let ep=res.data.payload.projType.exitProj;
                                if(ep==undefined||ep==null||ep==''){
                                    ep=[];
                                }
                                ep.forEach(item=>{
                                    item.timeAxis=this.changelinespandata(item.timeAxis);
                                    let rateDatalist=[];
                                    let rateData=this.changeFundRate(item.investOfReturnRate);
                                    let rateData1=this.changeFundRate(item.yearInvestOfReturnRate);
                                    rateData.name='投资收益率';
                                    rateData1.name="年均收益率";
                                    rateDatalist.push(rateData);
                                    rateDatalist.push(rateData1);
                                    item.rateDatalist=rateDatalist;
                                    //item.investOfReturnRateData=this.changeFundRate(item.investOfReturnRate)
                                    //item.yearInvestOfReturnRateData=this.changeFundRate(item.yearInvestOfReturnRate)
                                })
                                this.exitProj=ep;
                            }else{
                                this.projType_nodata=true;
                            }
                            
                            let dt=res.data.payload.goOutStatistical.detail;
                            this.detail=this.changelinespandata(dt);
                        }
                    }
                });
            },
            //获取基金列表
            getProOrFundlist(){
                allProjectOrFund({
                    type:2
                }).then(res=>{
                    if(res.data.msgNo==1){
                        this.fundList=res.data.payload.data;
                        if(this.fundList.length>0&&this.fundId==''){
                            this.fundId=this.fundList[0].id
                        }
                        this.getData();
                    }
                });
            },
            //修改选择基金
            changeFundOrPro(){
                this.getData();
            },
            //收益率数据处理
            changeFundRate(num){
                let data={};//type=>-1负数1=>正数
                if(num==null||num==undefined||num==''){
                    num=0
                }
                data.num=num;
                let ior=parseInt(num.toString().split('%')[0]);
                if(ior<0){
                    data.type=-1;
                    ior=-ior;
                }else{
                    data.type=1;
                }
                if(ior<100){
                    data.yieldrate_over_green='0';
                    data.yieldrate_over_orange='0';
                    if(data.type==-1){
                        data.yieldrate_normal_green=ior+'%';
                        data.yieldrate_normal_red='0';
                    }else{
                        data.yieldrate_normal_red=ior+'%';
                        data.yieldrate_normal_green='0';
                    }
                }else{
                    if(data.type==-1){
                        data.yieldrate_over_green='100%';
                        data.yieldrate_over_orange='0';
                        data.yieldrate_normal_green=10000/ior+'%';
                        data.yieldrate_normal_red='0';
                    }else{
                        data.yieldrate_over_green='0%';
                        data.yieldrate_over_orange='100%';
                        data.yieldrate_normal_green='0';
                        data.yieldrate_normal_red=10000/ior+'%';
                    }
                }
                return data;
            },
            //获取最大值
            getBiggestnum(list,name){
                let num=0;
                list.forEach(item=>{
                    if(item[name]>num){
                        num=item[name];
                    }
                })
                return num;
            },
            changelinespandata(list){
                let figure=this.getBiggestnum(list,'figure');
                list.forEach(item=>{
                    item.height=item.figure*60/figure+'px';
                })
                return list;
            },
            //清除百分号
            clearPercent(value){
                let arr=value.toString().split('%');
                return parseInt(arr[0])>100?100:parseInt(arr[0])
            },
            setElement(){
                let dom=document.getElementsByClassName('el_progress_blue');
                for(let j =0;j<dom.length;j++){
                    let dom1=dom[j].getElementsByClassName('el-progress-circle__track');
                    let dom2=dom[j].getElementsByClassName('el-progress-circle__path');
                    for(let i =0;i<dom1.length;i++){
                        dom1[i].setAttribute('stroke','#A1D3FF');
                        dom2[i].setAttribute('stroke-linecap','');
                    }
                }
                let dom3=document.getElementsByClassName('el_progress_red');
                for(let j =0;j<dom3.length;j++){
                    let dom4=dom3[j].getElementsByClassName('el-progress-circle__track');
                    let dom5=dom3[j].getElementsByClassName('el-progress-circle__path');
                    for(let i =0;i<dom4.length;i++){
                        dom4[i].setAttribute('stroke','#FFE6E6');
                        dom5[i].setAttribute('stroke-linecap','');
                    }
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../style/fund.scss';
    .fund-items{
        justify-content:space-between;
        max-width: 1400px;
    }
</style>



